.glitch {
  color: white;
  font-size: 50px;
  position: relative;
  width: 250px;
  margin: 0 auto;
  font-family: "Source Code Pro", monospace;
}

.glitch:after {
  content: attr(data-text);
  position: absolute;
  left: 8px;
  text-shadow: -1px 0 red;
  top: 0;
  color: white;
  background: #263136;
  overflow: hidden;
  clip: rect(0, 500px, 0, 0);
  animation: noise-anim 2s infinite linear alternate-reverse;
}

.glitch:before {
  content: attr(data-text);
